
import XHeader from 'vux/src/components/x-header/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'

import loadingBox from '@/components/LoadingBox'
export default{
components:{
XHeader,Qrcode,
loadingBox
},
data(){
return{
show:{
post:false,
isLoading:true
},
qrcode:{qrcode:'123',timestamp:0}
}
},
mounted(){
this.getFetch()
},
methods:{
getFetch(){
let _this = this;
this.$api.get('/wechat/member/qrcode')
.then(function(data){
console.log(data.object);
_this.show.post = true
_this.show.isLoading = false
_this.qrcode = data.object
})
.catch(this.$errorHandle);
},
refreshQr(){
this.show.post=false
this.show.isLoading = true
this.getFetch()
}
}

}



