
import loadingBox from '@/components/LoadingBox'
import LineChart from './LineChart'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default{
components:{
loadingBox,
LineChart,
ButtonTab,
ButtonTabItem,
XHeader
},
data(){
return{
show:{
post:false,
isLoading:true
},
showData:null,
weightData:null,
heightData:null

}
},
mounted(){
this.getFetch();
},
methods:{
getFetch(){
let _this = this;
this.$api.get('/wechat/member/baby/height/11')
.then(function(data){
_this.show.post = true
_this.show.isLoading = false
_this.heightData = data.object
_this.showData = data.object
})
.catch(this.$errorHandle);
this.$api.get('/wechat/member/baby/weight/11')
.then(function(data){
_this.show.post = true
_this.show.isLoading = false
_this.weightData = data.object
})
.catch(this.$errorHandle);
},
getWeight(){
this.showData=this.weightData
},
getHeight(){
this.showData=this.heightData
}
}
}



